"use client";
import { useEffect, useRef, useState } from "react";
import MapboxDraw from "@mapbox/mapbox-gl-draw";
import mapboxgl from "mapbox-gl";
import axios from 'axios';

mapboxgl.accessToken = process.env.NEXT_PUBLIC_MAPBOX_TOKEN;

const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL;
const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 60000,
});

// Generate ~300 random global points
const generateRandomPoints = (count = 300) => {
  const points = [];
  for (let i = 0; i < count; i++) {
    const lat = Math.random() * 180 - 90;
    const lon = Math.random() * 360 - 180;
    points.push([lon, lat]);
  }
  return points;
};
const globalPoints = generateRandomPoints();

export default function BloomingMap() {
  const leftMapContainer = useRef(null);
  const rightMapContainer = useRef(null);
  const compareContainer = useRef(null);
  const leftMapRef = useRef(null);
  const rightMapRef = useRef(null);
  const compareMapRef = useRef(null);
  const sliderRef = useRef(null);

  const [ndviData, setNdviData] = useState([]);
  const [frame, setFrame] = useState(0);
  const [globalCompareMode, setGlobalCompareMode] = useState(false);

  const [selectedLeftDate, setSelectedLeftDate] = useState(null);
  const [selectedRightDate, setSelectedRightDate] = useState(null);

  const [selectedRegion, setSelectedRegion] = useState(null);
  const [regionData, setRegionData] = useState([]);
  const [sliderPosition, setSliderPosition] = useState(50);
  const [regionalCompareMode, setRegionalCompareMode] = useState(false);
  
  // For date picker in regional mode
  const [beforeDateIndex, setBeforeDateIndex] = useState(0);
  const [afterDateIndex, setAfterDateIndex] = useState(0);
  const [showDatePicker, setShowDatePicker] = useState(false);

  // ‚úÖ Fetch global NDVI
  useEffect(() => {
    const fetchNDVI = async () => {
      try {
        const res = await api.get("/ndvi/global");
        console.log("Global NDVI response:", res.data);
        
        const data = res.data.data || res.data;
        
        if (Array.isArray(data) && data.length > 0) {
          setNdviData(data);
        } else {
          console.error("Invalid NDVI data format:", data);
        }
      } catch (err) {
        console.error("Error fetching NDVI:", err);
        console.error("Error details:", err.response?.data);
      }
    };
    fetchNDVI();
  }, []);

  const generatePulseData = (ndviValue, date) => ({
    type: "FeatureCollection",
    features: globalPoints.map(([lon, lat]) => ({
      type: "Feature",
      geometry: { type: "Point", coordinates: [lon, lat] },
      properties: {
        ndvi: ndviValue,
        date,
        intensity: Math.max(0, Math.min(1, (ndviValue - 0.28) / 0.22)),
      },
    })),
  });

  const addPulseLayer = (map, sourceId, layerId, data) => {
    if (!map.isStyleLoaded()) {
      map.once('load', () => addPulseLayer(map, sourceId, layerId, data));
      return;
    }

    if (!map.getSource(sourceId)) {
      map.addSource(sourceId, { type: "geojson", data });
    } else {
      map.getSource(sourceId).setData(data);
    }

    if (!map.getLayer(layerId)) {
      map.addLayer({
        id: layerId,
        type: "circle",
        source: sourceId,
        paint: {
          "circle-radius": ["interpolate", ["linear"], ["get", "intensity"], 0, 2, 1, 10],
          "circle-color": [
            "interpolate",
            ["linear"],
            ["get", "intensity"],
            0, "#60a5fa",
            0.3, "#facc15",
            0.6, "#22c55e",
            1, "#15803d",
          ],
          "circle-opacity": 0.6,
          "circle-blur": 0.7,
        },
      });
    }
  };

  const addRegionalNDVILayer = (map, data, sourceId, layerId) => {
    if (!data) return;
    
    if (!map.isStyleLoaded()) {
      map.once('load', () => addRegionalNDVILayer(map, data, sourceId, layerId));
      return;
    }

    // Check if data has geojson or if it's raw data we need to visualize
    let geojson = data.geojson;
    
    // If no geojson, create a circle to represent the point
    if (!geojson && selectedRegion?.lat && selectedRegion?.lon) {
      geojson = {
        type: "FeatureCollection",
        features: [{
          type: "Feature",
          geometry: {
            type: "Point",
            coordinates: [selectedRegion.lon, selectedRegion.lat]
          },
          properties: {
            ndvi: data.ndvi || 0,
            date: data.date
          }
        }]
      };
    }

    if (!geojson) return;
    
    if (!map.getSource(sourceId)) {
      map.addSource(sourceId, { type: "geojson", data: geojson });
    } else {
      map.getSource(sourceId).setData(geojson);
    }

    // Determine layer type based on geometry
    const geometryType = geojson.features[0]?.geometry?.type;

    if (!map.getLayer(layerId)) {
      if (geometryType === "Point") {
        // For point data, use circles with size based on NDVI
        map.addLayer({
          id: layerId,
          type: "circle",
          source: sourceId,
          paint: {
            "circle-radius": [
              "interpolate",
              ["linear"],
              ["get", "ndvi"],
              0, 20,
              0.5, 40,
              1, 60
            ],
            "circle-color": [
              "interpolate",
              ["linear"],
              ["get", "ndvi"],
              0, "#ef4444",
              0.2, "#f97316",
              0.4, "#facc15",
              0.6, "#84cc16",
              0.8, "#22c55e",
              1, "#15803d",
            ],
            "circle-opacity": 0.6,
            "circle-stroke-width": 2,
            "circle-stroke-color": "#ffffff"
          },
        });
      } else {
        // For polygon data, use fill
        map.addLayer({
          id: layerId,
          type: "fill",
          source: sourceId,
          paint: {
            "fill-color": [
              "interpolate",
              ["linear"],
              ["get", "ndvi"],
              0, "#ef4444",
              0.2, "#f97316",
              0.4, "#facc15",
              0.6, "#84cc16",
              0.8, "#22c55e",
              1, "#15803d",
            ],
            "fill-opacity": 0.7,
          },
        });
        
        // Add outline
        if (!map.getLayer(layerId + "-outline")) {
          map.addLayer({
            id: layerId + "-outline",
            type: "line",
            source: sourceId,
            paint: {
              "line-color": "#ffffff",
              "line-width": 2,
            },
          });
        }
      }
    }
  };

  // ‚úÖ Regional comparison map setup
  useEffect(() => {
    if (regionalCompareMode && compareContainer.current && regionData.length > 0) {
      if (!compareMapRef.current) {
        const compareMap = new mapboxgl.Map({
          container: compareContainer.current,
          style: "mapbox://styles/mapbox/satellite-v9",
          center: [selectedRegion.lon || 0, selectedRegion.lat || 0],
          zoom: 12,
        });
        compareMapRef.current = compareMap;

        compareMap.on("load", () => {
          updateComparisonView();
        });
      }
      return () => {
        if (compareMapRef.current) {
          compareMapRef.current.remove();
          compareMapRef.current = null;
        }
      };
    }
  }, [regionalCompareMode, regionData]);

  const updateComparisonView = () => {
    if (!compareMapRef.current || regionData.length === 0) return;
    if (!compareMapRef.current.isStyleLoaded()) {
      compareMapRef.current.once('load', updateComparisonView);
      return;
    }

    const beforeData = regionData[beforeDateIndex];
    const afterData = regionData[afterDateIndex];

    // Add both layers
    addRegionalNDVILayer(
      compareMapRef.current,
      beforeData,
      "before-source",
      "before-layer"
    );

    addRegionalNDVILayer(
      compareMapRef.current,
      afterData,
      "after-source",
      "after-layer"
    );

    // Create clip path effect using map bounds
    const canvas = compareMapRef.current.getCanvas();
    const width = canvas.width;
    const clipX = (sliderPosition / 100) * width;

    // Use paint property to fade layers based on position
    if (compareMapRef.current.getLayer("after-layer")) {
      // This is a simplified approach - we'll use opacity transition
      const afterOpacity = sliderPosition / 100;
      const beforeOpacity = 1 - (sliderPosition / 100);
      
      compareMapRef.current.setPaintProperty("before-layer", "circle-opacity", beforeOpacity * 0.6);
      compareMapRef.current.setPaintProperty("after-layer", "circle-opacity", afterOpacity * 0.6);
      
      // For fill layers
      if (regionData[0]?.geojson?.features[0]?.geometry?.type !== "Point") {
        compareMapRef.current.setPaintProperty("before-layer", "fill-opacity", beforeOpacity * 0.7);
        compareMapRef.current.setPaintProperty("after-layer", "fill-opacity", afterOpacity * 0.7);
      }
    }
  };

  useEffect(() => {
    if (regionalCompareMode && compareMapRef.current) {
      updateComparisonView();
    }
  }, [beforeDateIndex, afterDateIndex, sliderPosition, regionalCompareMode]);

  // ‚úÖ Init maps & interactions
  useEffect(() => {
    if (!leftMapContainer.current) return;

    const createMap = (container) => {
      const map = new mapboxgl.Map({
        container,
        style: "mapbox://styles/mapbox/light-v11",
        center: [0, 20],
        zoom: 1.5,
      });
      return map;
    };

    const leftMap = createMap(leftMapContainer.current);
    leftMapRef.current = leftMap;

    if (globalCompareMode && rightMapContainer.current) {
      const rightMap = createMap(rightMapContainer.current);
      rightMapRef.current = rightMap;
    }

    const draw = new MapboxDraw({
      displayControlsDefault: false,
      controls: { 
        polygon: true, 
        trash: true 
      },
      styles: [
        {
          'id': 'gl-draw-polygon-fill',
          'type': 'fill',
          'paint': {
            'fill-color': '#10b981',
            'fill-opacity': 0.3
          }
        },
        {
          'id': 'gl-draw-polygon-stroke-active',
          'type': 'line',
          'paint': {
            'line-color': '#10b981',
            'line-width': 3
          }
        },
        {
          'id': 'gl-draw-line',
          'type': 'line',
          'paint': {
            'line-color': '#10b981',
            'line-width': 3
          }
        },
        {
          'id': 'gl-draw-polygon-and-line-vertex-active',
          'type': 'circle',
          'paint': {
            'circle-radius': 6,
            'circle-color': '#ffffff',
            'circle-stroke-color': '#10b981',
            'circle-stroke-width': 2
          }
        }
      ]
    });
    leftMap.addControl(draw, 'top-left');

    leftMap.on("click", async (e) => {
      const { lng, lat } = e.lngLat;

      new mapboxgl.Marker({ color: "lime" }).setLngLat([lng, lat]).addTo(leftMap);

      try {
        const res = await api.get("/ndvi/point", {
          params: { 
            lat, 
            lon: lng, 
            start: "2024-03-01", 
            end: "2024-05-31", 
            buffer: 1000 
          },
        });
        setSelectedRegion({ lat, lon: lng });
        setRegionData(res.data.data);
        setBeforeDateIndex(0);
        setAfterDateIndex(res.data.data.length - 1);
        setShowDatePicker(true);
      } catch (err) {
        console.error("Error fetching point NDVI:", err);
      }
    });

    leftMap.on("draw.create", async (e) => {
      const coords = e.features[0].geometry.coordinates[0];
      const lons = coords.map((c) => c[0]);
      const lats = coords.map((c) => c[1]);
      const bbox = [Math.min(...lons), Math.min(...lats), Math.max(...lons), Math.max(...lats)];

      try {
        const res = await api.get("/ndvi/regional", {
          params: { bbox: bbox.join(","), start: "2024-03-01", end: "2024-05-31" },
        });
        setSelectedRegion({ bbox, lon: (bbox[0] + bbox[2]) / 2, lat: (bbox[1] + bbox[3]) / 2 });
        setRegionData(res.data.data);
        setBeforeDateIndex(0);
        setAfterDateIndex(res.data.data.length - 1);
        setShowDatePicker(true);
      } catch (err) {
        console.error("Error fetching regional NDVI:", err);
      }
    });

    return () => {
      if (leftMapRef.current) {
        leftMapRef.current.remove();
        leftMapRef.current = null;
      }
      if (rightMapRef.current) {
        rightMapRef.current.remove();
        rightMapRef.current = null;
      }
    };
  }, [globalCompareMode]);

  // ‚úÖ Animate pulses (stop when in regional compare mode)
  useEffect(() => {
    if (!leftMapRef.current || ndviData.length === 0 || regionalCompareMode || showDatePicker) return;

    if (globalCompareMode && selectedLeftDate !== null && selectedRightDate !== null) {
      const leftData = ndviData[selectedLeftDate];
      const rightData = ndviData[selectedRightDate];

      if (leftData) {
        const pulseData = generatePulseData(leftData.ndvi, leftData.date);
        addPulseLayer(leftMapRef.current, "ndvi-source-left", "ndvi-layer-left", pulseData);
      }

      if (rightData && rightMapRef.current) {
        const pulseData = generatePulseData(rightData.ndvi, rightData.date);
        addPulseLayer(rightMapRef.current, "ndvi-source-right", "ndvi-layer-right", pulseData);
      }
    } else if (!globalCompareMode) {
      let i = 0;
      const interval = setInterval(() => {
        i = (i + 1) % ndviData.length;
        setFrame(i);

        const { ndvi, date } = ndviData[i];
        const pulseData = generatePulseData(ndvi, date);

        addPulseLayer(leftMapRef.current, "ndvi-source-left", "ndvi-layer-left", pulseData);
      }, 1500);

      return () => clearInterval(interval);
    }
  }, [ndviData, globalCompareMode, selectedLeftDate, selectedRightDate, regionalCompareMode, showDatePicker]);

  const handleSliderDrag = (e) => {
    if (!compareContainer.current) return;
    const rect = compareContainer.current.getBoundingClientRect();
    const x = Math.max(0, Math.min(e.clientX - rect.left, rect.width));
    const percentage = (x / rect.width) * 100;
    setSliderPosition(percentage);
  };

  return (
    <div className="flex w-full h-screen relative">
      {!regionalCompareMode ? (
        <>
          <div ref={leftMapContainer} className="flex-1" />
          {globalCompareMode && (
            <div ref={rightMapContainer} className="flex-1 border-l-2 border-gray-400" />
          )}
        </>
      ) : (
        <div className="relative w-full h-full">
          <div ref={compareContainer} className="w-full h-full" />
          
          <div
            ref={sliderRef}
            className="absolute top-0 bottom-0 w-1 bg-white shadow-2xl cursor-ew-resize z-20"
            style={{ left: `${sliderPosition}%`, transform: 'translateX(-50%)' }}
            onMouseDown={(e) => {
              e.preventDefault();
              const handleMouseMove = (moveEvent) => {
                handleSliderDrag(moveEvent);
              };
              const handleMouseUp = () => {
                document.removeEventListener("mousemove", handleMouseMove);
                document.removeEventListener("mouseup", handleMouseUp);
              };
              document.addEventListener("mousemove", handleMouseMove);
              document.addEventListener("mouseup", handleMouseUp);
            }}
          >
            <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 bg-white rounded-full p-3 shadow-2xl border-2 border-gray-300">
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2.5">
                <path d="M18 8L22 12L18 16M6 8L2 12L6 16" />
              </svg>
            </div>
          </div>

          <div className="absolute top-4 left-4 bg-gradient-to-r from-blue-600 to-blue-500 text-white px-4 py-2 rounded-lg shadow-lg">
            <p className="text-sm font-bold">üìÖ {regionData[beforeDateIndex]?.date}</p>
            <p className="text-xs opacity-90">NDVI: {regionData[beforeDateIndex]?.ndvi?.toFixed(3) || 'N/A'}</p>
          </div>
          <div className="absolute top-4 right-4 bg-gradient-to-r from-green-600 to-green-500 text-white px-4 py-2 rounded-lg shadow-lg">
            <p className="text-sm font-bold">üìÖ {regionData[afterDateIndex]?.date}</p>
            <p className="text-xs opacity-90">NDVI: {regionData[afterDateIndex]?.ndvi?.toFixed(3) || 'N/A'}</p>
          </div>

          <div className="absolute bottom-4 left-1/2 -translate-x-1/2 bg-black/80 text-white px-4 py-2 rounded-lg text-xs">
            ‚Üê Slide to compare | {Math.round(100 - sliderPosition)}% Before | {Math.round(sliderPosition)}% After ‚Üí
          </div>
        </div>
      )}

      {/* Instructions Panel */}
      {!regionalCompareMode && !showDatePicker && (
        <div className="absolute top-4 left-4 bg-white/95 backdrop-blur rounded-xl shadow-xl p-4 max-w-xs">
          <h3 className="font-bold text-gray-800 mb-2 flex items-center gap-2">
            <span className="text-lg">üó∫Ô∏è</span>
            How to Use
          </h3>
          <div className="space-y-2 text-sm text-gray-700">
            <div className="flex items-start gap-2">
              <span className="text-blue-500 font-bold">1.</span>
              <p><strong>Click anywhere</strong> on the map to analyze a specific point</p>
            </div>
            <div className="flex items-start gap-2">
              <span className="text-green-500 font-bold">2.</span>
              <p><strong>Draw a region:</strong> Click the <span className="inline-block w-5 h-5 border-2 border-green-500 rounded"></span> polygon tool (top-left), then click multiple points to draw. Double-click to finish.</p>
            </div>
            <div className="flex items-start gap-2">
              <span className="text-purple-500 font-bold">3.</span>
              <p>Select dates to compare vegetation changes over time</p>
            </div>
          </div>
        </div>
      )}

      {/* HUD */}
      {!regionalCompareMode && !showDatePicker && ndviData.length > 0 && (
        <div className="absolute bottom-4 left-1/2 -translate-x-1/2 bg-white/80 px-4 py-2 rounded-lg shadow">
          <p className="text-sm font-semibold">
            üìÖ {ndviData[frame]?.date} | üå± NDVI: {ndviData[frame]?.ndvi.toFixed(3)}
          </p>
        </div>
      )}

      <div className="absolute top-4 right-4 flex flex-col gap-2">
        {!regionalCompareMode && !showDatePicker && (
          <button
            onClick={() => {
              setGlobalCompareMode(!globalCompareMode);
              if (!globalCompareMode) {
                setSelectedLeftDate(0);
                setSelectedRightDate(ndviData.length - 1);
              }
            }}
            className="bg-emerald-500 text-white px-4 py-2 rounded-lg shadow-lg hover:bg-emerald-600"
          >
            {globalCompareMode ? "Exit Global Compare" : "Global Compare"}
          </button>
        )}
        
        {regionalCompareMode && (
          <button
            onClick={() => {
              setRegionalCompareMode(false);
              setShowDatePicker(false);
            }}
            className="bg-red-500 text-white px-4 py-2 rounded-lg shadow-lg hover:bg-red-600"
          >
            Exit Regional Compare
          </button>
        )}
      </div>

      {/* Date Picker Modal */}
      {showDatePicker && regionData.length > 0 && !regionalCompareMode && (
        <div className="absolute inset-0 bg-black/50 flex items-center justify-center z-50">
          <div className="bg-white rounded-2xl p-8 max-w-2xl w-full mx-4 shadow-2xl">
            <h2 className="text-2xl font-bold mb-6 text-gray-800">Select Dates to Compare</h2>
            
            <div className="space-y-6">
              <div>
                <label className="block text-sm font-semibold text-gray-700 mb-2">
                  Before Date (Older Period)
                </label>
                <select
                  value={beforeDateIndex}
                  onChange={(e) => setBeforeDateIndex(parseInt(e.target.value))}
                  className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-500 focus:outline-none"
                >
                  {regionData.map((item, idx) => (
                    <option key={idx} value={idx}>
                      {item.date} - NDVI: {item.ndvi?.toFixed(3) || 'N/A'}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-semibold text-gray-700 mb-2">
                  After Date (Recent Period)
                </label>
                <select
                  value={afterDateIndex}
                  onChange={(e) => setAfterDateIndex(parseInt(e.target.value))}
                  className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-green-500 focus:outline-none"
                >
                  {regionData.map((item, idx) => (
                    <option key={idx} value={idx}>
                      {item.date} - NDVI: {item.ndvi?.toFixed(3) || 'N/A'}
                    </option>
                  ))}
                </select>
              </div>
            </div>

            <div className="flex gap-4 mt-8">
              <button
                onClick={() => setShowDatePicker(false)}
                className="flex-1 px-6 py-3 bg-gray-200 text-gray-700 rounded-lg font-semibold hover:bg-gray-300"
              >
                Cancel
              </button>
              <button
                onClick={() => {
                  setShowDatePicker(false);
                  setRegionalCompareMode(true);
                }}
                className="flex-1 px-6 py-3 bg-gradient-to-r from-blue-500 to-green-500 text-white rounded-lg font-semibold hover:from-blue-600 hover:to-green-600"
              >
                Compare ‚Üí
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Global comparison controls */}
      {globalCompareMode && ndviData.length > 0 && (
        <div className="absolute bottom-4 left-1/2 -translate-x-1/2 w-[80%] max-w-4xl bg-black/80 p-6 rounded-2xl shadow-2xl">
          <div className="grid grid-cols-2 gap-8">
            <div>
              <h3 className="text-white text-lg mb-3">Left Map Period</h3>
              <input
                type="range"
                min="0"
                max={ndviData.length - 1}
                value={selectedLeftDate}
                onChange={(e) => setSelectedLeftDate(parseInt(e.target.value))}
                className="w-full h-2 bg-gray-700 rounded-full appearance-none cursor-pointer"
              />
              <div className="mt-2 text-sm text-gray-400">
                {ndviData[selectedLeftDate]?.date} ‚Üí NDVI {ndviData[selectedLeftDate]?.ndvi.toFixed(3)}
              </div>
            </div>

            <div>
              <h3 className="text-white text-lg mb-3">Right Map Period</h3>
              <input
                type="range"
                min="0"
                max={ndviData.length - 1}
                value={selectedRightDate}
                onChange={(e) => setSelectedRightDate(parseInt(e.target.value))}
                className="w-full h-2 bg-gray-700 rounded-full appearance-none cursor-pointer"
              />
              <div className="mt-2 text-sm text-gray-400">
                {ndviData[selectedRightDate]?.date} ‚Üí NDVI {ndviData[selectedRightDate]?.ndvi.toFixed(3)}
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}